import React, { useState } from "react";
import { Trans } from "gatsby-plugin-react-i18next";
import ArrowDown from "../Ui/Svg/arrow_down.svg";
import useLanguage from "../../Hooks/useLanguage";

const OrderForm = () => {
  const lang = useLanguage;
  const [name, setName] = useState("");
  const [phone, setPhone] = useState("");
  const [products, setProducts] = useState(
    lang("Select", "", "Whlen", "Selezionare")
  );
  const [materials, setMaterials] = useState(" - ");
  const [comments, setComments] = useState("");
  const [readAndAccepted, setReadAndAccepted] = useState(false);
  const [error, setError] = useState(false);
  const [successfulSendingOfData, setSuccessfulSendingOfData] = useState(false);
  // const selects = <Trans>Select</Trans>;
  const sendingDataToMail = () => {
    fetch("http://localhost:1111/get_price_form", {
      method: "POST",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        name: name,
        phone: phone,
        products: products,
        materials:
          materials === " - " ? " " : materials,
        comments: comments,
      }),
    }).then(res => {
      res.json();
      res.status === 200
        ? setSuccessfulSendingOfData(true)
        : setSuccessfulSendingOfData(false);
    });
  };

  return (
    <div className="order-form">
      <label className="order-label">
        <span className="label-title">
          <Trans>Name</Trans> *
        </span>
        <input
          type="text"
          value={name}
          onChange={e => setName(e.target.value)}
        />
      </label>
      <label className="order-label">
        <span className="label-title">tel./cell. *</span>
        <input
          type="tel"
          value={phone}
          onChange={e => setPhone(e.target.value)}
          onKeyPress={evt => {
            let theEvent = evt || window.event;
            let key = theEvent.keyCode || theEvent.which;
            key = String.fromCharCode(key);
            let regex = /^(\d)$/g;

            if (!regex.test(key)) {
              theEvent.returnValue = false;
              if (theEvent.preventDefault) theEvent.preventDefault();
            }
          }}
        />
      </label>
      <label className="order-label">
        <span className="label-title">
          <Trans>Products</Trans> *
        </span>
        <span className="select-wrapper">
          <select onBlur={el => setProducts(el.target.value)}>
            <option value={lang("Select", "", "Whlen", "Selezionare")}>
              {lang("Select", "", "Whlen", "Selezionare")}
            </option>
            <option value={lang("WINDOWS", "", "FENSTER", "FINESTRE")}>
              {lang("WINDOWS", "", "FENSTER", "FINESTRE")}
            </option>
            <option
              value={lang("SLIDING", "", "RUTSCHEN", "SCORREVOLI")}
            >
              {lang("SLIDING", "", "RUTSCHEN", "SCORREVOLI")}
            </option>
            <option value={lang("DOORS", "", "TREN", "PORTE")}>
              {lang("DOORS", "", "TREN", "PORTE")}
            </option>
            <option
              value={lang(
                "MOSQUITO NETS",
                "",
                "MCKENNETZ",
                "ZANZARIERE"
              )}
            >
              {lang("MOSQUITO NETS", "", "MCKENNETZ", "ZANZARIERE")}
            </option>
            <option
              value={lang("SHUTTERS", "", "FENSTERLDEN", "PERSIANE")}
            >
              {lang("SHUTTERS", "", "FENSTERLDEN", "PERSIANE")}
            </option>
            <option
              value={lang("DUMPSTER", "", "MLLCONTAINER", "CASSONETTO")}
            >
              {lang("DUMPSTER", "", "MLLCONTAINER", "CASSONETTO")}
            </option>
            <option
              value={lang("SHUTTERS", "", "FENSTERLDEN", "TAPPARELLE")}
            >
              {lang("SHUTTERS", "", "FENSTERLDEN", "TAPPARELLE")}
            </option>
          </select>
          {products === lang("Select", "", "Whlen", "Selezionare") &&
          error ? (
            <div style={{ color: "red" }}>
              <Trans>Required field</Trans>
            </div>
          ) : null}
          <span className="select-arrow">
            <ArrowDown />
          </span>
        </span>
      </label>
      <label className="order-label">
        <span className="label-title">
          <Trans>Material</Trans> *
        </span>
        <span className="select-wrapper">
          <select onBlur={el => setMaterials(el.target.value)}>
            <option value=" - ">
              {lang("Select", "", "Whlen", "Selezionare")}
            </option>
            <option value="pvc">PVC</option>
            <option value="aluminium">Aluminium</option>
            <option value="legno">Legno</option>
          </select>
          {materials === " - " && error ? (
            <div style={{ color: "red" }}>
              <Trans>Required field</Trans>
            </div>
          ) : null}
          <span className="select-arrow">
            <ArrowDown />
          </span>
        </span>
      </label>
      <label className="order-label order-label__message">
        <span className="label-title">
          <Trans>Comment</Trans>
        </span>
        <textarea
          name="comments"
          value={comments}
          onChange={e => setComments(e.target.value)}
        ></textarea>
      </label>
      <label className="order-label order-label__text">
        <span className="check-wrapper">
          <input
            type="checkbox"
            onChange={() => setReadAndAccepted(!readAndAccepted)}
            className="checkbox"
          />
          <div className="custom"></div>
        </span>
        <p className="form-text">
          Desidero ricevere informazioni su promozioni, prodotti e servizi
          offerti da Oknoplast Sp. z o.o. e dai suoi rivenditori tramite
          dispositivi elettronici di comunicazione o terminali di
          telecomunicazione (ad es. newsletter, e-mail, SMS, telefono).
        </p>
      </label>
      <button
        onClick={() => {
          if (
            products === " - " ||
            materials === " - "
          ) {
            setError(true);
          } else {
            sendingDataToMail();
            setName("");
            setPhone("");
            setProducts(" - ");
            setMaterials(" - ");
            setComments("");
            setReadAndAccepted(false);
            setError(false);
          }
        }}
        disabled={
          !name || !phone || !products || !materials || !readAndAccepted
        }
        className="submit-button"
      >
        <Trans>Send</Trans>
      </button>
      {successfulSendingOfData ? (
        <div style={{ color: "green", textAlign: "center", marginTop: "15px" }}>
          <Trans>Thank you</Trans>
        </div>
      ) : null}
    </div>
  );
};

export default OrderForm;
